import { Form, Formik } from "formik";
import React, { useState, useEffect, createContext } from "react";
import { useDispatch } from "react-redux";
import { Link, useHistory } from "react-router-dom";
import Formikcontrol from "../../formik/Formikcontrol";
import cp from "../../img/cp.svg";
import card from "../../img/card.svg";
import { BiArrowBack } from "react-icons/bi";
import {
  getPlans,
  initialValues,
  packageOptions,
  validationSchema,
} from "../../helpers/invest";
import Crypto from "./Crypto";
import Card from "./Card";
import { deposit } from "../../redux/actions/deposit";
import { clearMessage } from "../../redux/actions/message";

export const payContext = createContext();

const Payment = () => {
  const [payMethod, setPayMethod] = useState("");
  const [step, setsStep] = useState(1);
  const [proofOfPay, setProofOfPay] = useState("");
  const [packageOption, setPackageOption] = useState({
    name: "",
    id: null,
    roi: null,
    minAmount: null,
    maxAmount: null,
  });

  const dispatch = useDispatch("");
  console.log(packageOption);
  console.log(packageOptions)
  const history = useHistory()

  useEffect(() => {
    getPlans();
    dispatch(clearMessage());
  }, []);

  const onSubmit = async (values) => {
    const data = { ...values, packageOption, proofOfPay };
    console.log(data);
    dispatch(deposit(data));

    setTimeout(() => {
      dispatch(clearMessage());
      history.push('/dashboard')
    }, 5000);
  };

  return (
    <div>
      <div className="w-9/15 md:w-7/10 m-auto mt-24 shadow-lg rounded-lg border border-gray-100 py-8 px-8 md:px-16">
        <h2 className="text-3xl text-center text-gray-600 tracking-wider">
          Invest
        </h2>

        {step === 1 && (
          <div className="my-5">
            <div className="border-b flex justify-between items-center border-gray-300 p-2 py-3">
              <div className="flex items-center">
                <input
                  type="radio"
                  className="form-radio mr-2"
                  name="payment_opt"
                  onClick={() => setPayMethod(1)}
                  text=""
                />
                <label htmlFor="crypto">Pay with Cryptocurrency</label>
              </div>

              <div>
                <img src={cp} alt="cryptocurrency" />
              </div>
            </div>
            <div className="border-b flex justify-between items-center border-gray-300 p-2 py-3">
              <div className="flex items-center">
                <input
                  type="radio"
                  className="form-radio mr-2"
                  name="payment_opt"
                  onClick={() => setPayMethod(2)}
                />
                <label htmlFor="card">Pay with Card</label>
              </div>

              <div>
                <img src={card} alt="card" />
              </div>
            </div>
            <div className="text-right mt-8">
              <button
                disabled={payMethod === ""}
                className="button"
                onClick={() => setsStep(2)}
              >
                next
              </button>
            </div>
          </div>
        )}
        <Formik
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={onSubmit}
        >
          {(formik) => (
            <Form>
              {/* {console.log(formik)} */}
              <div>
                {step === 2 && (
                  <div>
                    <div className="my-4">
                      <Formikcontrol
                        control="input"
                        type="email"
                        name="email"
                        className="form-input border-gray-300 rounded w-full"
                        placeholder="Email Address"
                      />
                    </div>
                    <div>
                      <h3>Please select your preferred investment plan</h3>
                      <div className="grid">
                        {/* <Formikcontrol
                          control="radio"
                          name="packageOption"
                          options={packageOptions}
                        /> */}
                        {packageOptions.map((option) => (
                          <div
                            className="flex items-center my-1.5 text-gray-700"
                            key={option._id}
                          >
                            <input
                              color="teal"
                              className="form-radio mr-2.5"
                              type="radio"
                              id={option._id}
                              name={option.name}
                              value={option._id}
                              checked={packageOption.id === option._id}
                              onChange={() =>
                                setPackageOption({
                                  ...packageOption,
                                  id: option._id,
                                  name: option.name,
                                  roi: option.roi,
                                  minAmount: option.minAmount,
                                  maxAmount: option.maxAmount,
                                })
                              }
                            />
                            <label htmlFor={option.name}>{option.name}</label>
                          </div>
                        ))}
                      </div>

                      <div className="my-4">
                        {packageOptions
                          .filter((option) => option._id === packageOption.id)
                          .map((opt) => (
                            <div key={opt._id}>
                              <Formikcontrol
                                control="input"
                                name="amount"
                                placeholder={`${opt.name} package. deposit an amount between  $${packageOption.minAmount} - $${packageOption.maxAmount}`}
                              />
                              <div className="text-right mt-8">
                                {formik.values.email !== "" &&
                                  formik.values.amount !== "" &&
                                  isNaN(formik.values.amount) === false &&
                                  parseFloat(formik.values.amount) >=
                                    opt.minAmount &&
                                  parseFloat(formik.values.amount) <=
                                    opt.maxAmount && (
                                    <div className="flex justify-between items-center">
                                      <div>
                                        <Link
                                          to="#"
                                          onClick={() => setsStep(1)}
                                        >
                                          <BiArrowBack className="w-10 h-8 text-gray-500" />
                                        </Link>
                                      </div>
                                      <div>
                                        <Link
                                          to="#"
                                          onClick={() => setsStep(3)}
                                          className="button"
                                        >
                                          next
                                        </Link>
                                      </div>
                                    </div>
                                  )}
                              </div>
                            </div>
                          ))}
                      </div>
                    </div>
                  </div>
                )}
                {step === 3 && payMethod === 1 && (
                  <Crypto
                    handleProofOfPay={setProofOfPay}
                    proofOfPay={proofOfPay}
                    amount={formik.values.amount}
                    handleStep={setsStep}
                  />
                )}
                {step === 3 && payMethod === 2 && <Card />}
              </div>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
};

export default Payment;
